$nav-transition: $transition;
$nav-primary-colour: $primary-colour;

#navigation {

	position: fixed;
	z-index: 9999;
	top: 0;
	left: -100%;

	width: 80%;
	height: 100vh;
	background: #303030;

	overflow: auto;
	transition: $nav-transition;

	@include md {

		position: absolute;
		left: auto;
		top: 70px;
		right: 0;

		width: 75%;
		height: auto;
		background: none;

		overflow: visible; }

	&.active {

		left: 0;

		@include md {

			left: auto; } }

	a {

		color: white;

		&:hover {

			text-decoration: none;
			color: white; } }

	.switcher {

		cursor: pointer; }

	.container {

		padding: 30px;
		color: white;

		@include md {

			padding: 0; }

		&:after {

			content: "";
			clear: both;
			display: table; }

		.search {

			@include md {

				position: absolute;
				top: -40px;
				right: 50px;

				width: 180px; }

			&:after {

				content: "";
				display: table;
				clear: both; }

			input {

				float: left;

				width: 80%;
				height: 30px;

				padding: 4px 8px;
				background: white;
				border: none;

				@include md {

					float: right;

					background: none;
					color: white;

					&::-webkit-input-placeholder {
						color: white;
						opacity: .85; }

					&::-moz-placeholder {
						color: white;
						opacity: .85; }

					&:-ms-input-placeholder {
						color: white;
						opacity: .85; }

					&:-moz-placeholder {
						color: white;
						opacity: .85; } } }


			label {

				float: right;

				width: 15%;
				height: 30px;

				background-image: url("img/static/magnify.png");
				background-position: center right;
				background-size: 20px;
				background-repeat: no-repeat;

				@include md {

					float: left; } } }

		.lang {

			position: absolute;
			top: -30px;
			right: 0;

			color: white;
			font-size: 14px;

			cursor: pointer;

			span.current {

				padding-right: 26px;

				background-image: url("img/static/angle.png");
				background-position: center right;
				background-size: 14px;
				background-repeat: no-repeat; }

			ul.dropdown {

				display: none;

				position: absolute;
				top: 18px;
				right: 0;

				margin: 0;

				background: white;
				border: 2px solid $nav-primary-colour;

				&.active {

					display: block; }

				li {

					&:hover {

						background: lightgray; }

					a {

						display: block;
						padding: 4px 18px;
						color: black; } } } }



		ul.nav {

			margin: 24px 0;

			@include md {

				float: right;
				text-align: right;
				margin: 0; }

			li {

				margin: 12px 0;
				font-size: 13px;

				@include md {

					margin: 0; }

				li {

					margin: 12px 0; }

				&.primary-nav-element:last-of-type {

					.name {

						padding-right: 0 !important; } } }

			& > li {

				@include md {

					display: inline-block; }

				.nav-item {

					.title {

						height: 24px;

						& + .nav-sublist {

							@include md {

								position: absolute; } }

						.icon {

							position: absolute;

							display: flex;
							justify-content: center;
							align-items: center;

							width: 24px;
							height: 24px;

							img {

								max-height: 18px; } }

						span.name {

							float: left;

							width: 90%;
							line-height: 24px;

							padding-left: 32px;

							@include md {

								width: auto;
								text-align: left;
								padding-right: 4px; }

							@include lg {

								padding-right: 12px; } }

						.state {

							float: right;

							width: 10%;
							line-height: 24px;

							font-size: 18px;
							text-align: right;

							@include md {

								display: none; } }

						&:after {

							content: "";
							display: table;
							clear: both; } }

					ul.nav-sublist {

						padding-left: 32px;
						overflow: hidden;

						margin: 0;
						max-height: 0;

						transition: .35s all ease-in-out;

						&.expanded {

							max-height: 250px; }

						@include md {

							text-align: left; }

						li {

							&:last-child {

								margin-bottom: 0; } } } } } } } }



.hamburger-btn {

	position: absolute;
	top: 16px;
	right: 16px;

	width: 24px;
	height: 24px;

	cursor: pointer;
	transition: $nav-transition;

	&.disabled {

		opacity: 0;
		pointer-events: none; }

	.stripe {

		position: absolute;
		left: 5px;

		width: 14px;
		height: 2px;
		background: white;

		&:nth-child(1) {

			top: 6px; }

		&:nth-child(2) {

			top: 11px; }

		&:nth-child(3) {

			top: 16px; } }

	@include sm {

		display: none; } }

.hamburger-collapse-btn {

	position: absolute;
	top: 16px;
	right: 16px;

	width: 24px;
	height: 24px;

	cursor: pointer;
	transition: $nav-transition;

	background-image: url("../img/static/hamburger-collapse-btn.png");
	background-position: center;
	background-size: 14px;
	background-repeat: no-repeat;

	opacity: 0;
	pointer-events: none;

	&.active {

		opacity: 1;
		pointer-events: auto; }

	@include sm {

		display: none; } }
